								<td>{{ user_data.role }}</td>
							</tr>
							<tr>
								<td class="font-semibold">Company</td>
								<td>{{ user_data.company }}</td>
							</tr>
							</table>
						</div>
						<!-- /Information - Col 1 -->
					</div>
				</div>
				<div class="vx-col sm:w-1/2 w-full">
					<h3 class="flex items-center">
						<feather-icon svgClasses="w-6 h-6" icon="AlertCircleIcon" class="mr-2" />
						<span class="font-medium text-lg leading-none">Information</span>
					</h3>
					<vs-divider />
					<div class="vx-row">
						<!-- Information - Col 2 -->
						<div class="vx-col flex-1">
							<table>
							<tr>
								<td class="font-semibold">Birth date</td>
								<td>{{ user_data.birth_date }}</td>
							</tr>
							<tr>
								<td class="font-semibold">Mobile</td>
								<td>{{ user_data.mobile }}</td>
							</tr>
							<tr>
								<td class="font-semibold">Languages</td>
								<td>{{ user_data.languages.join(", ") }}</td>
