							</tr>
							</table>
						</div>
						<!-- /Information - Col 2 -->
					</div>
				</div>
		  	</div>

        <div class="vx-row">
          <div class="vx-col w-full flex justify-end pt-5">
            <vs-button icon-pack="feather" icon="icon-edit" class="mr-4" :to="{name: 'app-user-edit', params: { userId: $route.params.userId }}" @click.native="$router.push({ name: 'useredit' })">Edit</vs-button>
            <vs-button type="border" color="danger" icon-pack="feather" icon="icon-trash" @click="confirmDeleteRecord">Delete</vs-button>
          </div>
        </div>

      </vx-card>

      <!-- Permissions -->
