        <div class="vx-row">
          <div class="vx-col w-full">
            <div class="flex items-end px-3">
              <feather-icon svgClasses="w-6 h-6" icon="LockIcon" class="mr-2" />
              <span class="font-medium text-lg leading-none">Permissions</span>
            </div>
            <vs-divider />
          </div>
        </div>

        <div class="block overflow-x-auto">
          <table class="w-full permissions-table">
            <tr>
              <th class="font-semibold text-base text-left px-3 py-2" v-for="heading in ['Module', 'Read', 'Write', 'Create', 'Delete']" :key="heading">{{ heading }}</th>
            </tr>

            <tr v-for="(val, name) in user_data.permissions" :key="name">
              <td class="px-3 py-2">{{ name }}</td>
              <td v-for="(permission, name) in val" class="px-3 py-2" :key="name+permission">
                <vs-checkbox v-model="val[name]" class="pointer-events-none" />
              </td>
            </tr>
          </table>
        </div>

      </vx-card> -->

    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user_data: {
		id: 1,
		avatar: 'https://i.pinimg.com/originals/1e/25/5d/1e255d7f708d38f0b8aa5400c8848c89.jpg',
		username: 'username',
		name: 'Mark Polo',
		email: 'mark.polo@gmail.com',
		status: 'active',
		role: 'admin',
		company: 'Logistics INC',
		birth_date: 'mm-dd-yy',
		mobile: '+321 09 123 45 67',
		languages: ["English", "Arabic"],
		gender: 'male',
		contact: '+123 45 678 90 12',
		permissions: {
			users: {
				read: true,
				write: false,
				create: false,
				delete: false
			},
			posts: {
				read: true,
				write: true,
				create: true,
				delete: true
			},
			comments: {
				read: true,
				write: false,
				create: true,
				delete: false
			}
		}
	  }
    }
  },
  methods: {
    confirmDeleteRecord() {
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: `Confirm Delete`,
        text: `You are about to delete "${this.user_data.username}"`,
        accept: this.deleteRecord,
        acceptText: "Delete"
      })
    },
    deleteRecord() {
      this.showDeleteSuccess()
    },
    showDeleteSuccess() {
      this.$vs.notify({
        color: 'success',
        title: 'User Deleted',
        text: 'The selected user was successfully deleted'
      })
    }
  }
}

</script>

<style lang="scss">
.avatar-col {
  width: 8.5rem;
}

#page-user-view {
  table {
    td {
      vertical-align: top;
      min-width: 140px;
      padding-bottom: .8rem;
      word-break: break-all;
    }

    &:not(.permissions-table) {
      td {
        @media screen and (max-width:370px) {
          display: block;
        }
      }
    }
  }
}

</style>